import util from '@/components/amap-wx/js/util.js';
import sempNavbar from "@/components/semp-navbar.vue";

export default {
	components: {
		sempNavbar
	},
	data() {
		return {
			historyList: [],
			isHistory:true,
			list: [],
			flng: true,
			timer: null,			
			hospitalList:[],
			loadingType: 'more', //加载更多状态										
			keywords:'',
			pagenum:1,
			timer:null
			
		};
	},
	onLoad() {
		
		this.historyList = uni.getStorageSync('search:history');		
		const res = uni.getSystemInfoSync();				

	},
	
	methods: {
		navBack(){
			uni.navigateBack();
		},
		
		searchInput(e){
			this.keywords=e.detail.value;
			if (this.keywords=='') {
					this.hospitalList=[];
					this.isHistory = true;
					this.historyList= [];
					this.historyList = uni.getStorageSync('search:history');
											
				return;
			} else {	
				this.isHistory = false;
				this.getData("refresh");
			}
			
		},
		search(){			
			if (this.keywords=='') {
				this.isHistory = true;
				this.historyList = [];
				this.historyList = uni.getStorageSync('search:history');
				uni.showModal({
					title: '提示',
					showCancel:false,
					content: '请输入内容。',
					success: res => {
						if (res.confirm) {
							
						}
					}
				});
				return;
			} else {			
				util.setHistory(this.keywords);
				this.isHistory=false;				
				this.getData("refresh");				
			}
		},
		
		/**
		 * 列表点击
		 */
		listTap(item) {
			//item = JSON.parse(JSON.stringify(item));
			// 如果当前是历史搜索页面 ，那么点击不储存,直接 跳转			
			if (item.name==this.keywords) {				
				return;
			} else {
				this.isHistory=true;				
				this.keywords=item.name;
				this.hospitalList = [];
				this.getData("refresh");				
				 
			}
		},
		//搜索历史点击
		historyClick(e){			
			this.keywords=e;
			this.getData("refresh");
		},
	
		//详情
		hospitalClick(orgUuid){
			let paramsUrl="?orgUuid="+orgUuid;
			uni.navigateTo({
				url: '/pages/appointment/hospital-dept/hospital-dept'+paramsUrl
			});
		},
		getData(type='add'){
			if (this.loadingType == 'loading' || this.loadingType=='nomore') {
				return;
			}
			if (type == 'refresh') {
				
				this.pagenum = 1;
			}
			this.$globalMethod.ds_api({
				method: "m=S001&token=SemXlsuV0Ko2Ugrp&pagesize=20&pagenum="+this.pagenum,
				loadmsg:"医院搜索",
				showLoading:false,
				data: {					
					searchKey:this.keywords
				},
				success: (res) => {
					if(!res || res.data.data.length==0){
						this.loadingType="nomore";
						return
					}					
					let list=res.data.data;
					list.forEach((item)=>{	
						this.hospitalList.push(item);																								
					})
					
					
					this.loadingType = list.length>=20 ? 'more' : 'nomore';
					this.pagenum++;
				}
			});
	
		},
		
		
		/**
		 * 清理历史搜索数据
		 */
		clearSearch() {
			uni.showModal({
				title: '提示',
				content: '是否清理全部搜索历史？该操作不可逆。',
				success: res => {
					if (res.confirm) {
						this.historyList = util.removeHistory();
					}
				}
			});
		},
	
	},
	
	
	/**
	 * 点击软键盘搜索按键触发
	 */
	onNavigationBarSearchInputConfirmed(e) {
		//this.keywords=e.text;
		if (this.keywords=='') {
			this.isHistory = true;
			this.historyList = [];
			this.historyList = uni.getStorageSync('search:history');
			uni.showModal({
				title: '提示',
				content: '请输入内容。',
				success: res => {
					if (res.confirm) {
					}
				}
			});
			return;
		} else {			
			util.setHistory(this.keywords);
			this.showList=true;		
			this.getData('refresh');				
		}
	},	
	/**
	 *  点击导航栏 buttons 时触发
	 */
	
	onNavigationBarButtonTap(e) {
		if(e.index===0){
			
			this.searchInput()
		}	
	},
	//加载更多
	onReachBottom() {
		this.getData();
	},
}
